<template>
  <div>
    <PersonalHeader></PersonalHeader>
    <div class="centerBg">
      <div class="wrapper clearfix">
        <div class="centerLeft">
          <PersonalMenu class="fl" currentMenu="6"></PersonalMenu>
        </div>
        <div class="centerRight">
          <div class="centerTitle">
            <ul>
              <router-link to="/account">
                <li class="active">账户中心</li>
              </router-link>
              <router-link to="/investorManage">
                <li>投资者管理</li>
              </router-link>
              <router-link to="/leaveManage">
                <li>留言管理</li>
              </router-link>
            </ul>
          </div>
          <div class="centerContent">
            <div class="row">
              <label class="title">公司logo</label>
              <el-upload class="avatar-uploader" action="https://jsonplaceholder.typicode.com/posts/" :show-file-list="false" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
              <p class="clear"></p>
              <p class="beizhu">*仅支持JPG、GIF、PNG、JPEG、BMP格式 , 文件小于2M</p>
            </div>
            <div class="row clearfix">
              <label class="title">企业名称</label>
              <input class="textInp" type="text" placeholder="请输入企业名称" :value="data.cname">
            </div>
            <div class="row clearfix">
              <label class="title">手机号码</label>
              <p class="desc">18333156487</p>
            </div>
            <div class="row clearfix">
              <label class="title">品牌名</label>
              <input class="textInp" type="text" placeholder="请输入品牌名" :value="data.bname">
            </div>
            <div class="row clearfix">
              <label class="title">注册地址</label>
              <input class="textInp" type="text" placeholder="请输入注册地址" :value="data.address">
            </div>
            <div class="row clearfix">
              <label class="title">联系邮箱</label>
              <input class="textInp" type="text" placeholder="请输入联系邮箱" :value="data.email">
            </div>
<!--            <div class="keepBtn" >保存</div>-->
            <el-button type="primary" class="keepBtn">保存</el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import PersonalHeader from '@/components/PersonalHeader'
  import PersonalMenu from '@/components/PersonalMenu'
  import api from '@/api/personal-api'
  export default {
    name: "index.vue",
    data(){
      return{
        imageUrl: '',
        data:''
      }
    },
    mounted(){
      this.getData();
    },
    methods: {
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      },
      /**
       * 获取用户信息
       */
      getData(){
        api.getUserInfo().then(data => {
          this.data = data;
          this.imageUrl = data.logo;
        });
      }
    },
    components: {
      PersonalHeader,
      PersonalMenu
    }
  }
</script>

<style lang="less" scoped>
  .centerBg{
    width: 100%;
    height: auto;
    padding: 20px 0 100px;
    background: #F4F5FA;
  }
  .centerLeft{
    float: left;
    width: 230px;
  }
  .centerRight{
    float: right;
    width: 950px;
    min-height: 610px;
    background: #fff;
    .centerTitle{
      padding: 0 33px;
      height: 60px;
      border-bottom: 1px solid #E3E4E5;
      ul{
        li{
          height: 58px;
          line-height: 58px;
          float: left;
          color: #333;
          font-size: 16px;
          border-bottom: 2px solid #fff;
          margin-right: 50px;
        }
        li.active{
          border-bottom: 2px solid #4F74F4;
        }
      }
    }
    .centerContent{
      padding: 35px 0 0;
      .row{
        margin-bottom: 15px;
        .title{
          float: left;
          width: 100px;
          line-height: 40px;
          text-align: right;
          margin-right: 15px;
        }
        .beizhu{
          font-size: 12px;
          color: #999;
          margin: 15px 0 34px;
          padding-left: 117px;
        }
        .textInp{
          width: 338px;
          height: 38px;
          line-height: 38px;
          border: 1px solid #E3E4E5;
          border-radius:2px;
          text-indent: 15px;
        }
        .sex{
          height: 40px;
          line-height: 40px;
        }
        .desc{
          float: left;
          height: 40px;
          line-height: 40px;
          color: #000;
        }
      }
      .keepBtn{
        margin: 30px 0 0 230px;
      }

    }
  }
  .avatar-uploader{
    float: left;
    width: 94px;
    height: 94px;
    line-height: 94px;
    text-align: center;
    border: 1px solid #E3E4E5;
  }
  .el-upload {
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .el-upload img{
    display: block;
    width: 100%;
    height: 100%;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 94px;
    height: 94px;
    line-height: 94px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



